import {ProjectContent} from './Assets/ProjectContent.js';
import {AboutContent} from './Assets/AboutContent.js';

import eventEmitter from './EventEmitter.js';
const emitter = eventEmitter.emitter;
import events from '../utils/events.js';

import Preload from 'preload-it';

class ContentManager {

    constructor() {

        // this.contentCount = this.getFetchCount({viewContent:
        // [ProjectContent, AboutContent]
        // });

        // this.progressCounter = 0;

        // this.progress = 0;


    }

    initContent() {

        this.loadWorkContent({content: ProjectContent});
        // this.about = this.loadViewContent({content: AboutContent});

    }

    loadWorkContent({content}) {

        this.workPreload = Preload();

        const videos = ProjectContent.map((content) => {
            if(content.media.videoSrc) return content.media.videoSrc;
        });

        this.workPreload.fetch(videos);

        this.workPreload.onprogress = (event) => {
            emitter.emit(events.UPDATE_PROGRESS, event.progress);
        }

        this.workPreload.oncomplete = (data) =>  {

            const promises = ProjectContent.map((content) => {

                const src = this.workPreload.getItemByUrl(content.media.videoSrc).blobUrl;
                const promise = this.loadVideo({src});
                return promise;

            });

            Promise.all(promises).then((data) => {

                window.contentLoaded = true;

                this.projects = ProjectContent.map((content, i) => {

                    content.media.video = data[i];
                    return content

                });

                emitter.emit(events.CONTENT_LOADED);

            })

        }

    }

    loadVideo({src}) {

        return new Promise((resolve) => {

            const video = document.createElement('video');

            // video.addEventListener('loadeddata', () => {

            //     if(video.readyState >= video.HAVE_CURRENT_DATA) {
            //         console.log(video);
            //         resolve(video);
            //     }
                
            // });

            video.crossOrigin = "*";

            video.autoplay = true;
        
            video.width = 512;
                
            video.height = 512;
                
            video.setAttribute('webkit-playsinline', true);
                
            video.playsinline = true;
                
            video.muted = true;
                
            video.loop = true;

            video.src = src;

            video.load();

            resolve(video);

        });       
    
    }

    loadImage({src}) {

        return new Promise((resolve) => {

            const img = new Image();
        
            img.crossOrigin = "*";
                
            img.addEventListener('load' , () => {
                resolve(img)
            });
    
            img.src = src;

        });

    }

    getFetchCount({viewContent}) {

        let count = 0;

        viewContent.map((content) => {

            content.map((c) => {

                if(c.media.videoSrc) {
                    count++;
                }
    
                if(c.media.imageSrc) {
                    count++;
                }
    
            });

        })

        return count;

    }

    get Projects() {
        return this.projects;
    }

    get ProjectMedia() {
        return this.projects.map((project) => {
            return project.media;
        });
    }

    get About() {
        return this.about
    }

    get AboutMedia() {
        return this.about.map((about) => {
            return about.media;
        });
    }

}

const contentManager = new ContentManager();
export default contentManager;